.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 100;
    display: flex;
  }
  
  @media only screen {
    .overlay {
      overflow-y: scroll;
      justify-content: center;
      /*align-items: flex-start;*/
      padding: 3rem 0rem 0rem 0rem;
    }
  }
  
  @media screen and (max-width: 840px) {
    .overlay {
      /* justify-content: center; */
      align-items: center;
    }
  }